.habit__stat .react-calendar {
  width: 350px;
  max-width: 100%;
  background: white;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.125em;
}

.stat-label {
  @apply text-left pl-2 pb-4 pt-6 text-2xl;
}

.stat {
  @apply m-1;
}

.habit__stat .react-calendar__navigation {
  display: flex;
  height: 44px;
  margin-bottom: 1em;
}

.habit__stat .react-calendar__navigation button {
  min-width: 44px;
  background: none;
}
.habit__stat .react-calendar__navigation button:disabled {
  background-color: #f0f0f0;
}
.habit__stat .react-calendar__navigation button:enabled:hover,
.habit__stat .react-calendar__navigation button:enabled:focus {
  background-color: #e6e6e6;
}
.habit__stat .react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
}
.habit__stat .react-calendar__month-view__weekdays__weekday {
  padding: 0.5em;
}
.habit__stat .react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  font-weight: bold;
}

.habit__stat .react-calendar__year-view .react-calendar__tile,
.habit__stat .react-calendar__decade-view .react-calendar__tile,
.habit__stat .react-calendar__century-view .react-calendar__tile {
  padding: 2em 0.5em;
}
.habit__stat .react-calendar__tile {
  max-width: 100%;
  padding: 10px 6.6667px;
  background: none;
  text-align: center;
  line-height: 28px;
  border-radius: 0.5rem;
  border: 1px solid #fff;
  transition: all 0.3s ease;
}

.habit__stat .react-calendar__tile:enabled:hover {
  background-color: #e6e6e6;
}
.habit__stat .react-calendar__tile--now {
  background: #ffff76 !important;
}
.habit__stat .react-calendar__tile--now:enabled:hover {
  background: #ffffa9 !important;
}

.habit__stat .react-calendar__tile--marked {
  background: var(--habit-color) !important;
  color: #fff !important;
}
.habit__stat .react-calendar__tile--marked:enabled:hover {
  opacity: 0.8;
  background: var(--habit-color) !important;
}
