.main .react-calendar__month-view__weekdays,
.react-calendar__tile:disabled,
.main .marks .react-calendar__navigation,
.main .react-calendar__navigation__arrow:disabled {
  @apply hidden !important;
}

.main .react-calendar__tile {
  @apply whitespace-pre-line m-0 flex-none pl-[1.6rem] text-base  text-gray-500 font-medium cursor-default w-[57px] !important;
}

.main .habits__calendar .react-calendar {
  @apply flex;
}

.main .react-calendar__month-view__days {
  @apply flex-nowrap flex-row-reverse p-3 justify-end !important;
}

.main .habits__calendar .react-calendar__viewContainer {
  @apply basis-2/3 flex-grow flex-shrink ml-auto relative !important;
}

.main .habits__calendar .react-calendar__navigation {
  @apply basis-1/3 flex-grow flex-shrink-0 flex items-center;
}

.main .habits__calendar .react-calendar__navigation__arrow {
  @apply absolute top-1/2 text-xl transition-all translate-y-[-50%];
}

.main .habits__calendar .react-calendar__navigation__arrow polyline {
  @apply stroke-[#6b7280];
}

.main .habits__calendar .react-calendar__navigation__arrow:hover polyline {
  @apply stroke-[#4b5563];
}

.main .react-calendar__navigation__label {
  @apply text-[#4b5563];
}

.main .habits__calendar .react-calendar__navigation__prev-button {
  @apply -right-2;
}

.main .habits__calendar .react-calendar__navigation__next-button {
  @apply -left-2;
}

.main
  .habits__calendar
  .react-calendar__month-view__days__day--neighboringMonth {
  @apply text-green-500 cursor-pointer !important;
}

.main .habits__calendar .react-calendar__navigation__label {
  @apply sm:text-xl text-lg first-letter:capitalize text-left;
}

.main .habit {
  @apply m-[3px] overflow-x-auto touch-none;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* Internet Explorer 10+ */
}

.main .habit::-webkit-scrollbar {
  /* WebKit */
  width: 0;
  height: 0;
}

.main .habits__calendar {
  @apply overflow-x-auto;
}

@media screen and (min-width: 767.98px) {
  /* width */
  .main .habits__calendar::-webkit-scrollbar {
    @apply h-[6px];
  }

  /* Handle on hover */
  .main .habits__calendar::-webkit-scrollbar-thumb:hover {
    @apply bg-[#555];
  }
}
